import React, { useEffect } from 'react';
import { connect } from 'react-redux';
import { StyledList, StyledListItem } from './peopleList.styled';
import { fetchPeople, selectPerson, setDefaultBroken } from "../../../actions";
import NoSSR from '@mpth/react-no-ssr';
import Spinner from '../../spinner/spinner';


const PeopleList = ({ fetchPeople, people, selectPerson, isFetchingPeople, setDefaultBroken }) => {
  useEffect(() => {
    fetchPeople()
  }, [])


  return (
    <NoSSR>

      <StyledList>
        {isFetchingPeople ? <Spinner /> :
          <>
            {
              people.map(el =>
                <StyledListItem onClick={() => {selectPerson(el.name) ; setDefaultBroken()}}>{el.name}</StyledListItem>
              )
            }
          </>
        }

      </StyledList>
    </NoSSR>
  )
}

const mapStateToProps = (state) => {
  return {
    people: state.people,
    isFetchingPeople: state.isFetchingPeople
  }
}



export default connect(mapStateToProps, { fetchPeople, selectPerson, setDefaultBroken })(PeopleList);